import $withSizes from "react-sizes"

export const withSizes = Component =>
  $withSizes(sizes => ({
    window: sizes,
    isMobile: $withSizes.isMobile(sizes),
    isTablet: $withSizes.isTablet(sizes),
    isDesktop: $withSizes.isDesktop(sizes),
  }))(Component)

export const supportSVG = () => {
  try {
    window === undefined
  } catch {
    return true
  }
  return typeof window.SVGRect !== "undefined" || typeof SVGRect !== "undefined"
}

export const blobToSrc = blob => {
  try {
    const urlCreator = window.URL || window.webkitURL
    return urlCreator.createObjectURL(blob)
  } catch (e) {
    return ""
  }
}
/**
 * 
 * @param {Object} elm
 */
export function checkVisible(elm) {
  if (!elm) return false
  try {
    const rect = elm.getBoundingClientRect()
    //
    //document.documentElement.clientHeight Internet Explorer 8765
    const viewHeight = Math.max(
      document.documentElement.clientHeight,
      window.innerHeight
    )
    //bottom top
    //bottom0top-0
    return !(rect.bottom < 0 || rect.top - viewHeight >= 0)
  } catch {
    return false // ssr  window  document
  }
}
