
function identify(value) {
  return value
}

/**
 * reducer  reducer  reducer
 *  reducer reducer  actionType  reducer
 *  reducer   reducer  actionType
 *  reducer  actionType  reducer
 *  reducer  actionType 
 *
 * @param {any} actionType
 * @param {any} [reducer=identify]
 * @param {any} ctx  reducer 
 * @returns
 */
function handleAction(actionType, reducer = identify, ctx) {
  return (state, action) => {
    const { type } = action
    if (type && actionType !== type) {
      return state
    }
    return reducer.call(ctx, state, action)
  }
}

/**
 *
 *
 * @param {any} reducers
 * @returns
 */
function reduceReducers(...reducers) {
  /**
   *  @param {any} previous state
   *  @param {any} current action
   */
  return (previous, current) =>
    reducers.reduce(
      /**
       *
       *  @param {any} p state
       *  @param {any} r reducer
       */
      (p, r) => r(p, current),
      previous
    )
}

/**
 *  handlers  reducer  reducer  state
 *
 * @param {{ actionType:reducer }} handlers
 * @param {any} defaultState
 * @param {any} ctx   handle 
 * @returns
 */
function handleActions(handlers, defaultState, ctx) {
  const reducers = Object.keys(handlers).map(type =>
    handleAction(type, handlers[type], ctx)
  )
  //  reducer 
  //  reducers  state  reducer  actionType
  //  reducer  actionType  reducers 

  // 
  //  actionType  reducer  reducers  state  reducer  actionType reducers 
  const reducer = reduceReducers(...reducers)
  return (state = defaultState, action) => reducer(state, action)
}

export default handleActions